@use '../abstract/colors' as *;

.dark {

    &-bg {
        &-default { background-color: $dark_surface_default;}
        &-primary { background-color: $dark_surface_primary; }
        &-secondary { background-color: $dark_surface_secondary; }
        &-tertiary { background-color: $dark_surface_tertiary; }
        &-top { background-color: $dark_surface_top; }
    }

    &-txt {
        &-primary { color: #fff; }
    }
}

.light {

    &-bg {
        &-default { background-color: $light_surface_default;}
        &-primary { background-color: $light_surface_primary; }
        &-secondary { background-color: $light_surface_secondary; }
        &-tertiary { background-color: $light_surface_tertiary; }
        &-top { background-color: $light_surface_top; }
    }

    &-txt {
        &-primary { color: $dark_surface_default; }
    }
}