.inventory-row {
    grid-template-columns: 1fr 150px 150px 150px 100px !important;
    align-items: center;
    transition: 0.1s;

    &:not(.inventory-row-header) {
        &:hover { background-color: rgba(0, 0, 0, 0.2); }
        &:last-child { border-color: transparent !important; }
    }
}