#userActionBox {
    right: 0;
    top: calc(100% + 10px);
    width: 300px;
    opacity: 0;
    pointer-events: none;
    transform: translateY(10px);
    transition: 0.3s;
    border-radius: 5px;
    overflow: hidden;
    
    &.open {
        opacity: 1;
        pointer-events: auto;
        transform: translateY(0);
    }
}