@use '../abstract' as *;

.awe-select {
    &-options {
        z-index: 100;

        &-wrapper {
            border: 1px solid #ccc;

            .awe-option {
                transition: 0.2s;

                &:hover { background: #eee; }
                &.selected {

                    &::after {
                        content: '';
                        position: absolute;
                        height: 100%;
                        width: 7px;
                        background-color: $green;
                        left: 0;
                        top: 0;
                    }
                }
            }
        }
    }

    &.open {

        .awe-input-wrapper { border-radius: 7px 7px 0 0 }
    }
}

.awe-select-option-filter {
    padding: 10px;
    border: none;
    border-bottom: 1px solid #ccc;
    width: 100%;
}

.dark {
    .awe-select {
        
        &-options {
            box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.3);
    
            &-wrapper {
                border: none;

                input {
                    background-color: $dark_surface_secondary;
                    color: inherit;
                }

                .awe-option {
                    transition: 0.2s;
    
                    &:hover { background: $dark_surface_secondary; }
                }
            }
        }
    }

    .awe-select-option-filter { border-bottom: none; }
}