@use '../abstract' as *;

.awe-slider {
    * { box-sizing: content-box; }

    .track-container { max-height: 15px; }
    .track-node {
        height: 30px;
        
        .node-label {
            font-size: 10px;
            top: 0;
            transform: translateY(calc(-110% - 5px));
            pointer-events: none;
            padding: 3px 5px;
            border-radius: 3px;
            align-self: center;
            transition: $trn-fast;
            opacity: 0;
        }

        &:hover {
            .node-label { opacity: 1; }
        }
    }

    .track-thumb {
        width: 15px;
        height: 15px;
        top: 0;
        bottom: 0;
        border-radius: 100px;
        pointer-events: none;
        margin: auto;
        
        .thumb-value {
            font-size: 10px;
            top: 0;
            transform: translateY(-110%);
            pointer-events: none;
            padding: 3px 5px;
            border-radius: 3px;
            align-self: center;
            transition: $trn-fast;
            opacity: 0;
        }

        &::before {
            position: absolute;
            width: 100%;
            height: 100%;
            content: '';
            background-color: $green;
            border-radius: 100%;
            opacity: 0.2;
            transition: $trn-fast;
            pointer-events: none;
        }
    }

    .range-label {
        pointer-events: none;
    }

    &:hover {

        .track-thumb{
            .thumb-value {
                opacity: 1;
                transform: translateY(calc(-110% - 5px))
            }
            &::before { transform: scale(2); }
        }
    }
}