
.modal {
	position: fixed;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	transform: scale(0);
	pointer-events: none;
	transition: 0.01s;
	transition-delay: 0.5s;
    z-index: 100;
	animation-delay: 0s;
	animation-duration: 0.3s;
	
	.bgc-overlay {
		opacity: 0;
		background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.9));
		transition: opacity 0.2s;
		transition-delay: 0.3s;
	}

	.modal-height-control {
		width: 100%;
		height: 100%;
		overflow: auto;
		
		.modal-box {
			border-radius: 5px;
			box-shadow: 0 20px 30px -10px rgba(0, 0, 0, 0.2);
			opacity: 0;
			transform: scale(0.8) translateY(10px);
			transition: 0.3s;
			max-width: 100%;
			margin: auto;
			align-self: center;

			&.box {
				&-medium { min-width: 800px; }
	
				&-large { min-width: 1200px; }
			}
		}
	}

	&.open {
		transform: scale(1);
		pointer-events: auto;
		transition-delay: 0s;
		opacity: 0;
		pointer-events: none;

		.bgc-overlay {
			opacity: 1;
			transition-delay: 0s;
		}

		.modal-box {
			transition-delay: 0.2s;
			opacity: 1;
			transform: translateY(0px) scale(1);
		}

		&:last-of-type {
			opacity: 1;
			pointer-events: auto;
		}
	}
}

.modal2 {
	transition: 0.3s;
	transition-delay: 0s;

	.modal-box {
		min-width: 600px;

		&-md { min-width: 800px; }
		&-lg { min-width: 1200px; }
	}
}