
.tooltip-box {
	max-height: 100%;
	pointer-events: none;
	z-index: 9999;
	
	.tooltip-content {
		box-shadow: 0 5px 5px -2px rgba(0, 0, 0, 0.1);
		border-radius: 5px;
		overflow: hidden;
		max-width: 350px;
        padding: 4px 10px;
		z-index: 9999;
	}

	.pointer {

		> div {
			width: 10px;
			height: 10px;
			transform: rotate(45deg) translate(30%, -30%);
			box-shadow: 5px 5px 5px -2px rgba(0, 0, 0, 0.3);
		}
	}

	&.bottom {

		.pointer {

			> div {
				transform: rotate(45deg) translate(30%, 30%);
			}
		}
	}

	&.top {

		.pointer {

			> div {
				transform: rotate(45deg) translate(-30%, -30%);
			}
		}
	}

	&.left {

		.pointer {

			> div {
				transform: rotate(45deg) translate(-30%, 30%);
			}
		}
	}
}