#aweSelector {
    z-index: 6;
    transform: translateX(100%);
    transition: 0.25s linear;
    transition-delay: 0.25s;
    pointer-events: none;

    .awe-selector {
        &-selected,
        &-selection {
            max-width: 50%;
        }

        &-selected {
            background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.9));
        }
        
        &-selection {
            transition: 0.25s linear;
            transform: translateX(100%);
            z-index: 2;

            .awe-selector-option {
                transition: 0.3s;

                .indicator {
                    opacity: 0;
                    transition: 0.3s;
                    transform: translateX(-20px);
                }

                &:hover { box-shadow: 0 10px 10px -10px rgba(0, 0, 0, 0.2) }
                &.selected {
                    
                    .indicator {
                        opacity: 1;
                        transform: translateX(0);
                    }
                }
            }
        }
    }

    &.active {
        transform: translateX(0);
        transition-delay: 0s;
        pointer-events: auto;

        .awe-selector-selection {
            transition-delay: 0.25s;
            transform: translateX(0);
        }
    }
}