.timeline-h {
	display: flex;

	.timeline-bullet {

		.bullet {
			width: 15px;
			height: 15px;
			border-radius: 100%;
            background-color: #ccc;
            box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.3);
		}
	}

	&:not(:last-child) {
		.timeline-bullet {

			&::before {
				content: '';
				position: absolute;
				width: 3px;
				height: 100%;
				margin: auto;
				background: #999;
				left: 0;
				right: 0;
				top: 0;
			}
		}
	}
}