.awe-autosuggest {

    &.open {

        .awe-input-wrapper { border-radius: 7px 7px 0 0 }
    }
}

.awe-suggestion-box {
    z-index: 100;
        
    .awe-suggestion-options {
        transition: 0.3s;

        .option {
            transition: 0.2s;

            &:hover { opacity: 0.5; }
        }
    }
}