@use '../abstract' as *;

.awe-asset-input {
    border-radius: 7px;
    background-color: #fff;
    border: 1px solid $dark_surface_tertiary;

    .awe-asset-placeholder {

        img {
            max-width:120px;
            margin: 0 auto;
            transition: 0.3s;
        }

        &.loading {

            img { transform: scale(0.5); }
        }
    }

    .awe-asset-file {

        &.img-contain {
            left: 10px;
            top: 10px;
            height: calc( 100% - 20px );
            width: calc( 100% - 20px );
            filter: drop-shadow(0px 10px 10px rgba(0, 0, 0, 0.2))
        }

        &.img-cover {
            left: 0;
            top: 0;
            height: 100%;
            width: 100%;
        }
    }

    .awe-asset-multiple-files {
        z-index: 5;

        > div {
            height: calc(100% - 25px);
            width: 100%;
            left: 0;
            top: 25px;
        }

        .file-wrapper {
            margin: 10px;

            img {
                border: 1px solid #ccc;

                &.img-contain { background-color: #eee; }
            }

            .awe-asset-control {
                right: 0;
                top: 0;
                z-index: 7;
                transition: 0.2s;
                transform: translateX(100%);
            }

            &:hover {
                .awe-asset-control { transform: translateX(0); }
            }
        }
    }

    label {
        position: absolute;
        left: 5px;
        top: 0;
        font-size: 14px;
        transform: translateY(18px);
        transition: 0.3s;
        padding: 0 10px;
        opacity: 0.8;
        pointer-events: none;
        z-index: 6;
        transform-origin: 0% 50%;
    }

    input {
        display: none;
        pointer-events: none;
    }

    .asset-footer {
        border-radius: 0 0 7px 7px;
        overflow: hidden;
        color: #fff;

        .btn { border-radius: 0; }
    }

    &.active {

        label {
            left: 0;
            opacity: 0.8;
            transform: translate(2px, 2px) scale(0.75);
            color: $green;
        }

        .awe-asset-placeholder { opacity: 0 }

        &.with-error {
            .awe-input-label {
                color: $red;
            }
        }
    }
}

.dark {
    .awe-asset-input {
        background-color: $dark_surface_default;
        border-color: transparent;
    }
}