@use '../abstract' as *;

.awe-toggle {
	height: 25px;
	width: 50px;
	background: #e5e5e5;
	border-radius: 50px;

	div {
		width: 25px;
		height: 25px;
		border-radius: 25px;
		background: #ccc;
		transition: 0.2s;
	}

	&.active {

		div {
			transform: translateX(100%);
			background: $green;
		}
	}
}

.dark {
	
	.awe-toggle {
		background-color: $dark_surface_default;

		&.primary { background-color: $dark_surface_primary; }
	}
}