@use '../abstract/colors' as *;

.txt {

	//SETTINGS

	&-underlined {
		text-decoration: underline;
	}

	&-strike {
		text-decoration: line-through;

		&-element {
			position: relative;

			&::after {
				content: "";
				position: absolute;
				left: 0;
				top: 0;
				bottom: 0;
				width: 100%;
				height: 2px;
				background-color: $black;
				margin: auto 0;
			}
		}

		&-light {

			&::after {
				background-color: $lightGray;
			}
		}
	}

	&-lineheight-1 {
		line-height: 1;
	}

    &-nowrap { white-space: nowrap }
	&-of {

		&-ellipsis { text-overflow: ellipsis; }
	}
}

