.brw {
    &-1 { border-width: 1px; border-style: solid; }
    &-2 { border-width: 2px; border-style: solid; }
    &-3 { border-width: 3px; border-style: solid; }
    &-4 { border-width: 4px; border-style: solid; }
    &-5 { border-width: 5px; border-style: solid; }
    
    &-top {
        &-1 { border-top-width: 1px; border-top-style: solid; }
        &-2 { border-top-width: 2px; border-top-style: solid; }
        &-3 { border-top-width: 3px; border-top-style: solid; }
        &-4 { border-top-width: 4px; border-top-style: solid; }
        &-5 { border-top-width: 5px; border-top-style: solid; }
    }

    &-bottom {
        &-1 { border-bottom-width: 1px; border-bottom-style: solid; }
        &-2 { border-bottom-width: 2px; border-bottom-style: solid; }
        &-3 { border-bottom-width: 3px; border-bottom-style: solid; }
        &-4 { border-bottom-width: 4px; border-bottom-style: solid; }
        &-5 { border-bottom-width: 5px; border-bottom-style: solid; }
    }

    &-left {
        &-1 { border-left-width: 1px; border-left-style: solid; }
        &-2 { border-left-width: 2px; border-left-style: solid; }
        &-3 { border-left-width: 3px; border-left-style: solid; }
        &-4 { border-left-width: 4px; border-left-style: solid; }
        &-5 { border-left-width: 5px; border-left-style: solid; }
    }

    &-right {
        &-1 { border-right-width: 1px; border-right-style: solid; }
        &-2 { border-right-width: 2px; border-right-style: solid; }
        &-3 { border-right-width: 3px; border-right-style: solid; }
        &-4 { border-right-width: 4px; border-right-style: solid; }
        &-5 { border-right-width: 5px; border-right-style: solid; }
    }
}