.notification-tray {
	position: fixed;
	right: 30px;
	bottom: 0;
	max-height: 100vh;
	z-index: 100;
	
	.notif-card {
		width: 400px;
		box-shadow: 0 10px 10px -10px rgba(0, 0, 0, 0.5);
		transform: scaleX(0);
		transform-origin: 0 50%;
		animation: notifIn 0.4s ease-in 0s;
		animation-fill-mode: forwards;

		.color-indicator {
			position: absolute;
			right: 0;
			top: 0;
			height: 100%;
			width: 100%;
			animation: notifIndicatorIn 0.3s ease-out 0.4s;
			animation-fill-mode: forwards;
		}
	}

	@keyframes notifIn {
		from { transform: scaleX(0); }
		to { transform: scaleX(1); }
	}

	@keyframes notifIndicatorIn {
		from { max-width: 100%; }
		to { max-width: 10px; }
	}
}