@use '../abstract/colors' as *;

.awe-colorpicker {

    .color-picked > div {
        width: 20px;
        height: 20px;
        border-radius: 5px;
        box-shadow: 0 5px 10px -7px rgba(0, 0, 0, 0.5);
        border: 1px solid #eee;
    }
    
    .color-palette {
        left: 0;
        top: 0;
        transform: translateY(-100%);
        opacity: 0;
        transition: 0.3s;
        pointer-events: none;
        z-index: 10;

        &.in {
            pointer-events: auto;
            opacity: 1;
            transform: translateY(calc(-100% - 10px))
        }
    }
}

.dark {
    .color-picked > div { border: 1px solid $dark_surface_secondary; }
}