#table {
    box-shadow: 0 10px 20px -5px rgba(0, 0, 0, 0.1);
    border-radius: 7px;

    #tableHead {
        position: sticky;
        top: 50px;
        z-index: 3;
        box-shadow: 0 5px 10px 0 rgba(0, 0, 0, .2);
        overflow: auto;
        
        > div {
            min-width: 1400px;
        }

        .sortable {
            cursor: pointer;

            .sort-up{ transform: translateY(2px); }
            .sort-down { transform: translateY(-2px); }

            .sort-indicator { transition: 0.2s; }

            &:hover {

                .sort-indicator { transform: translateY(0); }
            }
        }

        &::-webkit-scrollbar { width: 0; height: 0; }
    }

    #tableRows {
        
        .table-row-wrapper {
            min-width: 1400px;

            .hover-efx {
                transition: 0.2s;
                opacity: 0;
                background-color: #000;
            }

            &:hover {

                .hover-efx { opacity: 0.05; }
            }

            &.loading {
                pointer-events: none;
                opacity: 0.5;
            }
        }

        .table-row {
            word-break: break-all;
        }
    }

    #tableFooter {
        
        &.dark {
            box-shadow: 0 -5px 10px 0 rgba(0, 0, 0, .2);
            margin-top: 10px;
        }
    }
}

#tableDrawer {
    right: 0;
    bottom: 0;
    height: 100%;
    z-index: 5;
    
    .drawer-section {
        width: 400px;
        border-radius: 7px 0 0 7px;
        max-height: 100%;
    }

    &.open { box-shadow: -5px 0 20px 0 rgba(0, 0, 0, 0.5); }
}

.dark {
    #table {
    
        #tableRows {
            
            .table-row-wrapper {

                &:hover {
    
                    .hover-efx { opacity: 0.2; }
                }
            }
        }
    }
}