@use '../abstract/font_sizes' as *;

.txt {
	// SIZE
	&-giantsize { font-size: $giantSize }
	&-oversized { font-size: $oversized }
	&-header {
		font-size: $h1;

		&-2 { font-size: $h2 }
	}
	&-large { font-size: $large }
	&-xlarge { font-size: $xlarge }
	&-xxlarge { font-size: $xxlarge }
	&-medium {
		font-size: $medium;

		&-large { font-size: $mediumLarge }
	}
	&-small { font-size: $small !important }
	&-xsmall { font-size: 11px }

	@media (max-width: 1440px) {
		&-giantsize {
			font-size: $oversized;
		}
		&-oversized {
			font-size: $h1;
		}
		&-header {
			font-size: $h2;
	
			&-2 {
				font-size: $xxlarge;
			}
		}
		
		&-large {
			font-size: $medium;
		}
		&-xlarge {
			font-size: $large;
		}
		&-xxlarge {
			font-size: $xlarge;
		}
		&-medium {
			font-size: $small;
		}
	}

	@media (max-width: 1240px) {
		&-header {
			font-size: $xxlarge;
	
			&-2 {
				font-size: $xlarge;
			}
		}
		
		&-large {
			font-size: $medium;
		}
	}

	@media (max-width: 767px) {
		&-giantsize {
			font-size: $h1;
		}
		&-oversized {
			font-size: $h2;
		}
		&-header {
			font-size: $xxlarge;
	
			&-2 {
				font-size: $xlarge;
			}
		}

		&-medium {
			font-size: $small;

			&-large {
				font-size: $medium;
			}
		}

		&-large {
			font-size: $medium;
		}

		&-xlarge {
			font-size: $medium;
		}

		&-xxlarge {
			font-size: $large;
		}

		&-small {
			font-size: 10px;
		}
	}
}