@use '../abstract' as *;

.awe-input-wrapper {
    border-radius: 7px;
    background-color: #fff;
    border: 1px solid $dark_surface_tertiary;
    color: inherit;

    .awe-input-control {
        border: none;
        outline: none;
        padding: 25px 15px 15px;
        font-size: 14px;
        background-color: transparent;
        min-width: 100px;
        transition: 0.3s;
        opacity: 0;
        color: inherit;
    }

    .awe-textarea {
        width: 100%;
        min-height: 200px;
        resize: none;
        font-family: $primaryFontRegular;
    }

    .awe-input-btn {
        background: transparent;
        border: none;

        > i {
            transition: 0.3s;
        }

        &:hover {

            > i {
                transform: scale(1.3);
            }
        }
    }

    .awe-input-label {
        position: absolute;
        left: 5px;
        top: 0;
        font-size: 14px;
        transform: translateY(18px);
        transition: 0.3s;
        padding: 0 10px;
        opacity: 0.8;
        pointer-events: none;
        transform-origin: 0% 50%;
        color: inherit;
    }

    .left {
        padding-top: 10px;
        transition: 0.3s;
    }
    
    &.active {

        .awe-input-label {
            opacity: 0.8;
            transform: translate(2px, 2px) scale(0.75);
            left: 0;
            color: $green;
        }

        .awe-input-control, .left { opacity: 1 }
        
        &.with-error {
            .awe-input-label {
                color: $red;
            }
        }
    }

    &.unlabeled {

        .left { padding: 0; }
        .awe-input-control {
            padding: 15px;
        }
    }
}

.dark {
    .awe-input-wrapper {
        background: $dark_surface_default;
        border-color: transparent;

        input::placeholder {
            color: #aaa;
            opacity: 1;
        }

        &.primary {
            background: $dark_surface_primary;
        }
    }
}