@use '/src/assets/scss/abstract/transition' as *;

.filter-box {
    right: 0;
    top: 0;
    height: 100%;
    width: 100%;
    max-width: 400px;
    z-index: 5;
    transform: translateX(100%);
    transition: $trn-medium;
    
    &.show {
        transform: translateX(0);
        box-shadow: -5px 0 20px 0 rgba(0, 0, 0, 0.2);
    }
}

.dark {
    &.show {
        box-shadow: -5px 0 20px 0 rgba(0, 0, 0, 0.5);
    }
}