@use '../abstract/colors' as *;

.bg {

	&-blue,
	&-primary {
		background: $bgBlue !important;
		color: black;

		&-light {
			background: $lightBlue !important;
		}

		&-pastel {
			background: $pastelBlue !important;
		}
	}

	&-green,
	&-success {
		background: $bgGreen !important;
		color: black;

		&-pastel {
			background: $pastelGreen !important;
		}
	}

	&-orange,
	&-warning {
		background: $orange !important;
		color: black;
		
		&-pastel {
			background: $pastelOrange !important;
		}

		&-dark {
			background: $pastelOrangeDark !important;
		}
	}

	&-red,
	&-error {
		background: $bgRed !important;

		&-pastel {
			background: $pastelRed !important;
		}
	}

	&-gray {
		background: $gray !important;

		&-dark {
			background: #666 !important;
		}

		&-light {
			background: $lightGray !important;
		}
	}

	&-222 {
		background: #222 !important;
	}

	&-333 {
		background: #333 !important;

		&-grad {
			background: linear-gradient(#333, #222) !important;

			&-rev {
				background: linear-gradient(#222, #333) !important;
			}
		}
	}

	&-666 {
		background: #666 !important;

		&-grad {
			background: linear-gradient(#666, #444) !important;

			&-rev {
				background: linear-gradient(#444, #666) !important;
			}
		}
	}

	&-white {
		background: #fff !important;
	}

	&-black {
		background: #000 !important;
	}

	&-faded {
		&-dark { background: rgba(0, 0, 0, 0.2) }
		&-light { background: rgba(255, 255, 255, 0.2) }
	}
}