@use './abstract/colors' as *;

.ellipsis-loader {
    display: flex;
    align-items: center;
    justify-content: center;

    div {
        width: 7px;
        height: 7px;
        border-radius: 100%;
        background: #fff;
        animation-name: ellipsisLoading;
        animation-duration: 1s;
        animation-iteration-count: infinite;
        margin: 0 10px;
    }

    // DOT COLOR
    &.dot-black {
        div {
            background: $lightGray;
        }
    }

    &.dot-orange {
        div {
            background: $orange;
        }
    }

    // DOT SIZE
    &.dot-medium {
        div {
            width: 12px;
            height: 12px;
        }
    }

    &.fullpage {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
    }

    &.el-black {
        background: $black;
    }

    @keyframes ellipsisLoading {
        from,
        to {
            transform: translateY(0);
        }

        50% {
            transform: translateY(-100%);
        }
    }
}