@use '../abstract' as *;

.awe-checkbox-wrapper {

    .check-btn {
        &::before {
            content: '';
            position: absolute;
            width: calc(100% - 4px);
            height: calc(100% - 4px);
            border: 1px dashed #aaa;
            left: 1px;
            top: 1px;
            transition: 0.3s;
            transform: scale(0.9);
            border-radius: 5px;
            background:rgba(0, 0, 0, 0.1);
        }

        .awe-checkbox {
            min-width: 25px;
            min-height: 25px;
            border: 2px solid #aaa;
            border-radius: 5px;
            transition: 0.1s;
            background: #fff;
        
            .check {
                left: 0;
                top: 0;
                transition: 0.2s;
                transform: rotate(-180deg);
                opacity: 0;
            }
        }   
    }

    &.checked {
        .check-btn {
            .awe-checkbox {
                background-color: $green;
                border-color: $green;
    
                .check {
                    transform: rotate(0deg);
                    opacity: 1;
                }
            }
    
            &::before {
                background:transparentize($color: $green, $amount: 0.8);
                border-color: $green;
            }
        }
        
        &.error {
            .check-btn {
                .awe-checkbox {
                    background-color: $red;
                    border-color: $red;
                }
        
                &::before {
                    background:transparentize($color: $red, $amount: 0.8);
                    border-color: $red;
                }
            }
        }
    }

    &:hover {
        .check-btn {
            &::before { transform: scale(1.7); }
        }
    }
}

.dark {
    .awe-checkbox-wrapper {
        &:not(.checked) {
            .check-btn {
                .awe-checkbox {
                    border: 2px solid $dark_surface_tertiary;
                    background: $dark_surface_default;
                }
            }   
        }
    }
}