@use '../abstract/colors' as *;

.awe-rte {
    height: 350px;
    padding-top: 25px;
    
	.awe-rte-editor {
		cursor: text;
		padding: 0 20px 10px;

        .DraftEditor-root {
            height: initial;
        }
	}

	.awe-rte-toolbar {
		background: none;
		transition: 0.3s;
        border-top: none;
        border-bottom: 1px solid #ccc;
        opacity: 0;
        z-index: 2;
        pointer-events: none;

		.awe-rte-option-wrapper {border-radius: 5px; }
        
        .rdw-dropdown-optionwrapper { overflow-y: auto; }

        &.active {
            opacity: 1;
            pointer-events: auto;
        }
	}
}

.dark {
    .awe-rte {

        .awe-rte-toolbar {
            border: none;
            background: $dark_surface_tertiary;
            border-radius: 5px;

            .rdw-dropdown-wrapper,
            .rdw-dropdown-optionwrapper {
                background-color: $dark_surface_default;
                border-color: $dark_surface_tertiary;
                box-shadow: none;
            }

            .rdw-dropdownoption-active,
            .rdw-dropdownoption-highlighted {
                background-color: $dark_surface_primary;
            }
        }
    }
}