@use '../abstract' as *;

.btn {
    box-shadow: none;
    border: none;
    border-radius: 5px;
	padding: 20px 30px;
    color: inherit;
    background: $gray;
    font-size: $mediumLarge;
    transition: $trn-medium;
    line-height: 1;
    min-width: 100px;
    cursor: pointer;
    display: inline-block;
    box-sizing: content-box;
    letter-spacing: 3px;

    &.no-letter-spacing { letter-spacing: initial; }
    &.btn-small { padding: 12px 30px; }
    
	&.with-icon {
		display: inline-flex;
		justify-content: center;
		align-items: center;
    }

    .btn-indicator {
        position: absolute;
        width: 17px;
        height: 17px;
        right: -7px;
        top: -7px;
        font-size: 12px;
        background: $orange;
        border: 2px solid #fff;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 100%;
        pointer-events: none;
    }
    
    // TYPE
    &-icon {
        min-width: 0;
        background: transparent;
        padding: 0;
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        letter-spacing: initial;
        position: relative;

        i {
            transition: $trn-fast;
            position: relative;
        }

        &::before {
            content: '';
            position: absolute;
            width: 40px;
            height: 40px;
            background-color: #fff;
            opacity: 0.1;
            border-radius: 100%;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            margin: auto;
            transform: scale(0);
            transition: 0.2s;
            pointer-events: none;
        }
        &:hover,
        &:focus-visible {

            i { transform: scale(1.2); }
            &::before { transform: scale(1); }
        }

        &-sm {
            width: 30px;
            height: 30px;

            &::before {
                width: 30px;
                height: 30px;
            }
        }
    }

    // BTN COLOR
    &.btn-primary {
        background: $bgBlue !important;
        border: 2px solid $bgBlue;
        color: #000;

        &:hover {
            opacity: 0.5;
        }
    }

    &.btn-secondary {
        background: #fff !important;
        border: 2px solid #fff !important;
        color: #333;
    }

    &.btn-red,
    &.btn-error {
        background: $bgRed;
        border: 2px solid $bgRed;
        color: #fff;
        
        &:hover {
            opacity: 0.5;
        }
    }

    &.btn-green,
    &.btn-success {
        background: $bgGreen;
        border: 2px solid $bgGreen;
        color: #000;
        
        &:hover {
            opacity: 0.5;
        }
    }

    &.btn-orange,
    &.btn-warning {
        background: $orange;
        border: 2px solid $orange;
        color: #fff;
        
        &:hover {
            opacity: 0.5;
        }
    }

    &.btn-gray {
        background: $gray;
        border: 2px solid $gray;

        &:hover {
            opacity: 0.5;
        }
    }

    &.btn-lgray {
        background: $lightGray;
        color: #111;
    }

    &.btn-mgray {
        background: $medGray;
        color: #111;
    }

    &.btn-violet {
        background: #C9287D;

        &:hover {
            color: #fff;
        }
    }

    &.btn-clear {
        background: transparent;
        min-width: 0;
        color: inherit;

        &-green {
            background: transparent;
            border: 2px solid $lGreen;
            color: $lGreen;

            &.no-border {
                border: 2px solid transparent;
            }

            &:hover {
                background: $lGreen;
                color: #fff;
            }
        }

        &-bordered {

            &-dark-blue {
                background: transparent;
                border: 2px solid $darkBlue;
                color: $darkBlue;

                &:hover {
                    background: $darkBlue;
                    color: #fff;
                }
            }

            &-light-gray {
                background: transparent;
                border: 1px solid #D6D6D6;
                color: $darkBlue;

                &:hover {
                    background: #D6D6D6;
                }

                &.active {
                    background: $darkBlue;
                    color: #fff;
                }
            }

            &-white {
                background: transparent;
                border: 2px solid #fff;
                color: #fff;

                &:hover {
                    background: #fff;
                    color: $darkBlue;
                }
            }

            &-green {
                background: transparent;
                border: 2px solid $green;
                color: #fff;

                &:hover {
                    background: $green;
                    color: #fff;
                }
            }

            &-red {
                background: transparent;
                border: 2px solid $bgRed;
                color: $red;

                &:hover {
                    background: $bgRed;
                    color: #fff;
                }
            }

            &-orange,
            &-warning {
                background: transparent;
                border: 2px solid $orange;
                color: #fff;

                &:hover {
                    background: $orange;
                    color: #fff;
                }
            }
        }

        &.black {
            color: $black;
            border: 1px solid $black;
        }

        &.clear-black {
            color: $black;
        }

        &.secondary {
            color: $black;
            border: 1px solid white;
        }

        &.white {
            color: white;
            border: 1px solid white;
        }

        &.link {
            color: #356077;
            border: 1px solid #356077;
        }

        &:hover {
            opacity: 0.5;
        }
    }

    &.btn-black {
        background: $black;

        &:hover {
            color: #fff;
        }
    }

    &.btn-white {
        background: #fff;
        color: $black;

        &:hover:not(:disabled) {
            background: transparent !important;
            border: 1px solid #fff !important;
            color: #fff !important;
        }
    }

    &.btn-oblivion {
        background: #000;
        
        &:hover {
            color: #fff;
        }
    }

    // BTN SIZES
    &.btn-sm {
        font-size: $small;
        padding: 5px 15px;
    }

    //BTN PAD SETTINGS
    &.btn-pad-large { padding: 10px 30px; }

    &.btn-pad-xlarge { padding: 15px 40px; }

    &.no-padding { padding: 0; }

    //--BTN SETTINGS
    &.btn-block {
        display: block;

        &-center {
            margin: auto;
        }
    }

    &.btn-round {
        border-radius: 100%;

        &-px { border-radius: 100px; }

        &-0 { border-radius: 0 }
    }

    &.btn-bold {
        font-weight: bold;
    }

    &.btn-nostyles {
        padding: 0;
        background: none;
        min-width: 0;
    }
    
    &:active:not(.no-transition) {
        opacity: 0.5;
    }

    &:disabled {
        opacity: 0.5 !important;
        pointer-events: none;
    }

    &-width-auto {
        min-width: 0;
        width: auto;
    }

    &-width-full {
        width: 100%;
    }
}

.btn:focus:not(:active) {
    box-shadow: none !important;
}

.underline-hover {
    position: relative;

    &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 2px;
        background: #fff;
        bottom: 0;
        left: 0;
        transform: scaleX(0);
        transform-origin: 0 50%;
        transition: $trn-fast;
    }

    &.uh-black {
        &::after {
            background: $black;
        }
    }

    &:hover,
    &.active {

        &::after {
            transform: scaleX(1);
        }
    }
}

.scale-hover {
    transition: $trn-fast;

    &:hover {
        transform: scale(1.2);
    }
}

.opacity-hover {
    transition: 0.3s;

    &:hover { opacity: 0.5 }
}

.custom-button-container {
	display: inline-block;

	.button-loader {
		position: absolute;
		right: 5px;
		top: 0;
		height: 100%;
	}
}

.clear-btn {
    border: none;
    background: none;
    box-shadow: none;
    color: inherit;
    text-align: inherit;
    transition: 0.3s;

    &:hover { opacity: 0.5 }
}

@media (max-width: 767px) {
    .btn {
        font-size: $small;
        
        &.btn-lg {
            font-size: $medium;
        }

        &.btn-pad-large {
            padding: 5px 15px;
        }
    
        &.btn-pad-xlarge {
            padding: 10px 15px;
        }
    }
}