@use './abstract' as *;

html {
	font-size: $medium;
	color: $black;
	overflow-x: hidden;
	// scroll-behavior: smooth;

	@media (max-width: 1440px) {
		font-size: $small;
	}
}

body {
	margin: 0;
	padding: 0;
	font-family: $primaryFontRegular;
	color: $black;
	letter-spacing: 0.7px;
	line-height: 1.4;
	
	* {
		box-sizing: border-box;
	}
	
	&.modal-open {
		// height: 100vh !important;
		padding-right: 0 !important;
	}
	

	input.awe-input-control:-webkit-autofill,
	input.awe-input-control:-webkit-autofill:hover,
	input.awe-input-control:-webkit-autofill:focus,
	input.awe-input-control:-webkit-autofill:active {
		background-color: transparent !important;
		transition: background-color 600000s 0s, color 600000s 0s;
	}

	input[data-autocompleted] {
		background-color: transparent !important;
	}

	&.dark {
		/* width */
		&::-webkit-scrollbar,
		.of-auto::-webkit-scrollbar,
		.rdw-dropdown-optionwrapper::-webkit-scrollbar { width: 7px; height: 7px; }
		/* Track */
		&::-webkit-scrollbar-track,
		.of-auto::-webkit-scrollbar-track,
		.rdw-dropdown-optionwrapper::-webkit-scrollbar-track { background: $dark_surface_default }

		/* Handle */
		&::-webkit-scrollbar-thumb,
		.of-auto::-webkit-scrollbar-thumb,
		.rdw-dropdown-optionwrapper::-webkit-scrollbar-thumb {
			background: #999;
			border: 1px solid $dark_surface_secondary;
		}
	}

	&.light {
		/* width */
		&::-webkit-scrollbar,
		.of-auto::-webkit-scrollbar,
		.rdw-dropdown-optionwrapper::-webkit-scrollbar { width: 7px; height: 7px; }
		/* Track */
		&::-webkit-scrollbar-track,
		.of-auto::-webkit-scrollbar-track,
		.rdw-dropdown-optionwrapper::-webkit-scrollbar-track { background: $light_surface_default }

		/* Handle */
		&::-webkit-scrollbar-thumb,
		.of-auto::-webkit-scrollbar-thumb,
		.rdw-dropdown-optionwrapper::-webkit-scrollbar-thumb {
			background: #999;
			border: 1px solid $dark_surface_secondary;
		}
	}
}

h1 { margin: 0; }

p {
	margin-bottom: 0;
	overflow-wrap: break-word;

	&:first-child {
		margin-top: 0;
	}
}

input {
	outline: none;
}

figure {
	
	img {
		max-width: 100%;
	}
}

button {
	outline: none !important;
	cursor: pointer;
}

a,
a:hover,
a:focus {
	color: inherit;
	text-decoration: none;
	outline: none;
}

a:not([href]):not([tabindex]):hover, a:not([href]):not([tabindex]):focus {
	color: inherit;
}

ul {
	padding-left: 0;
	margin: 0;
}

li { list-style: none; }