@use '/src/assets/scss/abstract' as *;

#dashboardNav {
    box-shadow: 0 0 10px 0 rgba(23, 50, 66, 0.2);
    z-index: 6;
    width: auto;
    left: 0;
    top: 0;
    height: 100vh;

    .nav-logo {
        padding: 20px 20px 10px;
        max-width: 255px;
        transition: 0.3s;

        img {
            width: 200px;
            height: 30px;
            object-fit: contain;
            filter: drop-shadow(0 3px 5px rgba(0, 0, 0, 0.5));
            transition: 0.3s;
        }

        &.toggled {
            max-width: 1px;
            overflow: hidden;
            
            img { max-width: 1px }
        }
    }

    .link-group {
        position: relative;
        transition: 0.2s;
        
        > :first-child {
            transition: 0.3s;
        }

        .link-icon { transition: 0.2s; }

        .badge-notif {
            position: absolute;
            right: 0;
            top: 0;
            height: 100%;
            background: $red;
            padding: 0 5px;
        }

        .module-import-progress {
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 3px;
            background: $green;
            transition: 0.2s;
            z-index: 2;
        }

        &::after,
        &::before {
            position: absolute;
            content: '';
            width: 7px;
            height: 100%;
            background: $red;;
            left: 0;
            top: 0;
            transform-origin: 0 50%;
            transform: scaleX(0);
            transition: 0.3s;
        }

        &::before {
            width: 100%;
            background: rgba(0, 0, 0, 0.1);
        }

        &.child-link {
            background: rgba(0, 0, 0, 0.2);

            &::after {
                background: $orange;
            }
        }

        &:hover,
        &.active {
            // font-weight: bold;
            
            .link-icon { transform: scale(1.1); }

            &::before { transform: scale(1); }
        }

        &.active {
            
            > :first-child { transform: translateX(5px); }

            // i { color: $red; }

            &.child-link {

                i { color: $orange; }
            }

            &::after { transform: scaleX(1); }
        }
    }

    &.dark {
        box-shadow: 0 0 10px 0 #000;
        background: $dark_surface_primary;

        .link-group {
            &::before {
                background: rgba(0, 0, 0, 0.2)
            }
    
            &.child-link {
                background: rgba(0, 0, 0, 0.4)
            }
        }
    }
}