@use '/src/assets/scss/abstract/colors' as *;

.strip-tracker {

    .tracker {
        height: 3px;
        width: 100%;
        max-width: 0;
        left: 0;
        bottom: 0;
        transform: translateY(50%);
        transition: 0.3s;
    }
}

.dark {

    .strip-tracker {
        border-bottom: 1px solid $dark-surface-tertiary;
    }
}