@use '../abstract' as *;

.btn2-wrapper {
    min-width: 100px;
}

.btn-2 {
    box-shadow: none;
    border: none;
    border-radius: 3px;
    color: inherit;
    background: $gray;
    font-size: $mediumLarge;
    transition: 0.3s;
    line-height: 1;
    width: 100%;
    cursor: pointer;
    display: flex;
    position: relative;
    overflow: hidden;
    align-items: unset;
    padding: 0;
    
    > div {

        &.btn-label {
            letter-spacing: 1.5px;
            white-space: nowrap;
            padding: 15px 25px;
        }

        &.btn-label-efx {
            max-width: 100%;
            transition: 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
            letter-spacing: 1.5px;

            &.efx-left {
                left: initial;
                right: 0;
            }
        }

        &.btn-filler {
            padding: 0;
            width: 20px;
            transition: 0.2s;
            position: relative;

            &::after,
            &::before {
                content: '';
                position: absolute;
                width: 100%;
                height: 100%;
                left: 0;
                top: 0;
                transition: 0.5s;
                transform-origin: 100% 50%;
                transform: scaleX(0);
            }

            &::after {
                background: linear-gradient(-63deg, #fff 50%, transparent 50%);
            }

            &::before {
                background: linear-gradient(63deg, transparent 50%, #fff 50%);
            }
        }

        &.btn-ic {
            background-color: #fff;
            transition: 0.2s;
            width: 40px;
            overflow: hidden;
            
            i { font-size: 1.2rem }
        }
    }

    &.large {
        > div {
            
            &.btn-label {
                padding-top: 20px;
                padding-bottom: 20px;
            }
            &.btn-ic { width: 60px; }
            &.btn-filler { width: 30px; }
        }
    }

    &.xsmall {
        > div {
            
            &.btn-label {
                padding-top: 8px;
                padding-bottom: 8px;
            }
            &.btn-ic { width: 30px; }
        }
    }
    // BTN COLOR
    &.btn-blue,
    &.btn-primary {
        background: $bgBlue;
        color: #000;
        border: 1px solid $bgBlue;

        .btn-ic { color: $blue; }
        .btn-label-efx { background: $bgBlue; }
    }

    &.btn-white {
        background: #fff;
        color: #111;
        border: 1px solid #aaa;
        
        .btn-label-efx {
            background: #fff;
            box-shadow: 0 0 0 1px #aaa;
        }
    }

    &.btn-red,
    &.btn-error {
        background: $bgRed;
        color: #fff;
        border: 1px solid $bgRed;

        .btn-ic { color: $red; }
        .btn-label-efx { background: $bgRed; }
    }

    &.btn-green,
    &.btn-success {
        background: $bgGreen;
        color: #000;
        border: 1px solid $bgGreen;

        .btn-ic { color: $green; }
        .btn-label-efx { background: $bgGreen; }
    }

    &.btn-orange,
    &.btn-warning {
        background: $orange;
        color: #fff;
        border: 1px solid $orange;

        .btn-label-efx { background: $orange; }
    }

    &.btn-gray {
        background: $gray;
        border: 1px solid $gray;

        .btn-ic { color: $gray; }
        .btn-label-efx {
            background: $gray;
            color: #fff;
        }
    }

    &.btn-lgray {
        background: $lightGray;
        color: #111;

        .btn-label-efx { background: $lightGray; }
    }

    &.btn-mgray {
        background: $medGray;
        color: #111;

        .btn-label-efx { background: $medGray; }
    }

    &.btn-black {
        background: $black;

        .btn-label-efx { background: $black; }

        &:hover {
            color: #fff;
        }
    }

    &:active {
        transition: 0s;
        opacity: 0.5;
    }

    &.sharp {
        border-radius: 0;
    }

    &:hover,
    &:focus-visible,
    &.loading {

        > div.btn-filler {
            transform: translateX(50%);
        }
        >div.btn-label-efx { max-width: calc(100% - 40px); }

        &.large {
            >div.btn-label-efx { max-width: calc(100% - 60px); }
        }

        &.xsmall {
            >div.btn-label-efx { max-width: calc(100% - 30px); }
        }
    }
}

.dark {
    .btn-2 {
        
        > div {
    
            &.btn-ic { background-color: $dark-surface-default; }
        }
    }
}