@use '../abstract/colors' as *;

.txt {

	// COLOR
	&-primary,
	&-blue {
		color: $blue !important;
	}

	&-error,
	&-red {
		color: $red !important;

		&-pastel {
			color: $pastelRed !important;
		}
	}

	&-success,
	&-green {
		color: $green !important;
	}

	&-warning,
	&-orange {
		color: $orange !important;
	}

	&-yellow {
		color: $yellow !important;
		
		&-light { color: $lYellow !important; }
	}

	&-gray {
		color: $gray;

		&-med {
			color: $medGray;
		}

		&-light {
			color: $lightGray;
		}

		&-dark {
			color: $dGray;
		}
	}

	&-white {
		color: #fff !important;
	}

	&-dark {
		color: #000;
	}
}
