@use '../../abstract/colors' as *;

.brc {
	&-gray {
		border-color: $gray;

		&-med {
			border-color: $medGray;
		}

		&-light {
			border-color: $lightGray;
		}

		&-dark {
			border-color: $dGray;
		}
	}

	&-white {
		border-color: #fff !important;
	}

	&-dark {
		border-color: $black;
	}
}

.dark {
    .brc {
        &-default { border-color: $dark_surface_default;}
        &-primary { border-color: $dark_surface_primary; }
        &-secondary { border-color: $dark_surface_secondary; }
        &-tertiary { border-color: $dark_surface_tertiary; }
        &-top { border-color: $dark_surface_top; }
    }
}

.light {
    .brc {
        &-default { border-color: $light_surface_default;}
        &-primary { border-color: $light_surface_primary; }
        &-secondary { border-color: $light_surface_secondary; }
        &-tertiary { border-color: $light_surface_tertiary; }
        &-top { border-color: $light_surface_top; }
    }
}