@use '../abstract/colors' as *;

.pagination-links {
	
	.pagination-btn {
		min-width: 30px;
		height: 30px;
		padding: 5px;
		border: none;
		border-radius: 7px;
		margin-left: 10px;
		transition: 0.2s;
		color: inherit;
		background-color: transparent;
		
		&::before {
			content: '';
			position: absolute;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			background:  $bgRed;
			transition: 0.2s;
			transform: scale(0);
		}

        &.filler {
            min-width: auto;
            padding: 0;
        }

		&:hover:not(.active),
		&:focus-visible:not(.active) {
			color: #fff;

			&::before {
				transform: scale(1);
				opacity: 0.2;
			}
		}

		&.active {
			color: #fff;

			&::before { transform: scale(1); }
		}
	}
}