@use './abstract/colors' as *;

.badge {
	border-radius: 100px;
	padding: 5px 10px;
	display: inline-block;
	
	&-primary {
		background: $blue;
		color: #fff;
	}

	&-warning {
		background: $orange;
		color: #fff;
	}

	&-success {
		background: $green;
		color: #fff;
	}

	&-error {
		background: $red;
		color: #fff;
	}

	&-white {
		background: #fff;
		color: #111 !important;
	}

    &-notif {
        position: absolute;
        top: 0;
        right: 0;
        transform: translateX(50%) translateY(-50%);
        pointer-events: none;
        padding: 3px 5px;
        letter-spacing: 1px;

        > .badge { padding: 3px 5px }
    }
}