#loginPage {

    #loginBox,
    #forgotBox {
        transition: transform 0.8s;
        max-width: 1200px;
        width: 100%;
        margin: auto;
        
        .captcha-box {
            max-width: 50%;
            margin-left: auto;
            margin-right: auto;
        }
    }

    #loginGreetings {
        position: fixed;
        width: 40%;
        height: 100%;
        top: 0;
        background: #111;
        animation-duration: 0.8s;
        animation-fill-mode: forwards;
        z-index: 5;

        video {
            display: none;
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            object-fit: cover;
            background: linear-gradient(90deg, #E63449, #f20c46, #F86A61);
        }

        .greeting-width-wrapper {
            position: absolute;
            height: 100%;
            top: 0;
            background: linear-gradient(90deg, #E63449, #f20c46, #F86A61);
            // background: rgba(0, 0, 0, 0.5);
            animation-duration: 0.8s;
            animation-fill-mode: forwards;

            .greeting-box {
                width: 40%;
                transition: 0.8s;

                &#signinGreet { transform: translateX(-50px); }
                &#forgotGreet { transform: translateX(50px); }

                &#signinGreet,
                &#forgotGreet {

                    &.active { transform: translateX(0); }
                }
            }

            .greeting-toggle {
                width: 40%;
                top: 100%;
                right: 0;
                transition: 0.8s;

                button {
                    min-width: 250px;
                    letter-spacing: 3px;
                    animation-duration: 0.8s;
                    animation-timing-function: linear;
                    overflow: hidden;
                    border: 2px solid #fff;
                    outline: none;
                    padding: 20px 50px;
                    background: none;
                    color: #fff;
                    border-radius: 100px;

                    .toggle-btn-label {
                        transition: 0.5s;

                        &.forgot-label { transform: translateX(50%); }
                        &.signin-label {
                            transform: translateX(-50%);
                            width: 100%;
                            left: 0;
                            top: 0;
                            height: 100%;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                        }

                        &.signin-label,
                        &.forgot-label {
                            opacity: 0;

                            &.active {
                                transition-delay: 0.3s;
                                transform: translateX(0);
                                opacity: 1;
                            }
                        }
                    }
                }

                &.toggle-signin {
                    transform: translateX(0);

                    button { animation-name: signinToggleWidth; }
                }
                &.toggle-forgot {
                    transform: translateX(-60vw);

                    button { animation-name: forgotToggleWidth; }
                }
            }
        }

        &.active {

            &-signin {
                left: 0;
                animation-name: greetingsSignin-HM, greetingsSignin-Width;

                .greeting-width-wrapper {
                    left: 0;
                    animation-name: greetingsSignin-inner-HM;
                }
            }

            &-forgot {
                right: 0;
                animation-name: greetinsForgot-HM, greetinsForgot-Width;

                .greeting-width-wrapper {
                    right: 0;
                    animation-name: greetinsForgot-inner-HM;
                }
            }
        }
    }
}

@keyframes greetinsForgot-HM {
    from { transform: translateX(0); }
    to { transform: translateX(-60vw); }
}
@keyframes greetingsSignin-Width {
    from { width: 40%; }
    20% { width: 60%; }
    to { width: 40%; }
}

@keyframes greetingsSignin-HM {
    from { transform: translateX(0); }
    to { transform: translateX(60vw); }
}

@keyframes greetinsForgot-Width {
    from { width: 40%; }
    20% { width: 60%; }
    to { width: 40%; }
}

@keyframes greetinsForgot-inner-HM {
    from { transform: translateX(0); }
    to { transform: translateX(60vw); }
}

@keyframes greetingsSignin-inner-HM {
    from { transform: translateX(0); }
    to { transform: translateX(-60vw); }
}

@keyframes signinToggleWidth {
    from { min-width: 250px }
    30% { min-width: 400px }
    60% { min-width: 400px }
    to { min-width: 250px; }
}

@keyframes forgotToggleWidth {
    from { min-width: 250px }
    30% { min-width: 400px }
    50% { min-width: 400px }
    to { min-width: 250px; }
}