#uniselector {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    // opacity: 0;
    pointer-events: none;
    transform: translateX(100%);
    transition: 0.4s;
    overflow: hidden;
    z-index: 10;

    .selector-header,
    .selector-footer {
        background: #fff;
        box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
        z-index: 2;
    }

    .selector-body {

        > .flex-1 {
            transition: 0.3s;
        }

        &.filtering {

            > .flex-1 {
                filter: blur(2px);
                transform: translateX(-100px);
            }
        }
    }

    .selection {
        background: #fff;
    }

    .selected {
        background: rgba(0, 0, 0, 0.5);
    }

    .disabled {
        opacity: 0.5;
    }

    &.open {
        // opacity: 1;
        pointer-events: auto;
        transform: translateX(0);
    }
}