.float-in-up-down {
    animation-name: floatInUpDown;
    animation-duration: 0.6s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-out;
    opacity: 0;
    transform: translateY(50%);
}

.float {
    animation-name: float;
    animation-duration: 2s;
    animation-timing-function: linear;

    &-min {
        transform: translateY(5%);
        animation-name: floatMin;
        animation-duration: 2s;
        animation-timing-function: linear;
    }
}

.sway {
    animation-name: sway;
    animation-duration: 2s;
}

.fade-in {
    animation-name: fadeIn;
    animation-duration: 1s;
    animation-fill-mode: forwards;
    animation-delay: 0.3s;
    opacity: 0;
}

.fade-in-up,
.fade-in-top {
    animation-name: fadeInUp;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
    opacity: 0;

    &.fade-50 {
        animation-name: fadeInUpHalfFull;
    }
}

.fade-out-up {
    animation-name: fadeOutUp;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
    opacity: 0;

    &.fade-50 {
        animation-name: fadeOutUpHalfFull;
    }
}

.fade-in-right {
    animation-name: fadeInRight;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
    opacity: 0;
}

.fade-in-left {
    animation-name: fadeInLeft;
    animation-duration: 1s;
    animation-fill-mode: forwards;
    opacity: 0;
}

.curtain-out-right {
    animation-name: curtainOutRight;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
    transform-origin: 100% 50%;
}

.fade-in-down,
.fade-in-bottom {
    animation-name: fadeInDown;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
    opacity: 0;
}

.pulse {
    animation-name: pulse;
    animation-duration: 0.3s;

    &-inwards {
        animation-name: pulse-inwards;
        animation-duration: 0.3s;
    }

    &-heart {
        animation-name: pulseHeart;
        animation-duration: 5s;
    }
}

.rotate-animation {
    animation-name: rotate;
    animation-duration: 5s;
    animation-timing-function: linear;
}

.slow-animation {
    animation-duration: 1s;
}

.infinite-animation {
    animation-iteration-count: infinite;
}

.reverse-animation {
    animation-direction: reverse;
}

.scale-in {
    animation-name: scaleIn;
    animation-duration: 0.5s;
    animation-duration: linear;
    animation-fill-mode: forwards;
    transform: scale(0);
}

.scale-x-in {
    animation-name: scaleXIn;
    animation-duration: 0.5s;
    transform-origin: 0 50%;
    animation-fill-mode: forwards;
    transform: scaleX(0);
}

.rock-h {
    animation-name: rockH;
    animation-timing-function: linear;
    animation-duration: 0.25s;
}

@keyframes moveUpDown {
    from,to {
        transform: translateY(0);
    }

    25% {
        transform: translateY(-100%);
    }

    50% {
        transform: translateY(0);
    }

    75% {
        transform: translateY(100%);
    }
}

@keyframes scaleIn {
    from {
        transform: scale(0);
    }

    25% {
        transform: scale(1.3);   
    }

    35% {
        transform: scale(1.4);
    }

    to {
        transform: scale(1);
    }
}

@keyframes scaleXIn {
    from {
        transform: scaleX(0);
    }

    to {
        transform: scaleX(1);
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes fadeInDown {
    from {
        opacity: 0;
        transform: translateY(-20%);
    }

    to {
        opacity: 1;
        transform: translateY(0)
    }
}

@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }

    to {
        opacity: 1;
        transform: translateY(0)
    }
}

@keyframes fadeInRight {
    from {
        opacity: 0;
        transform: translateX(-10%);
    }

    to {
        opacity: 1;
        transform: translateX(0)
    }
}

@keyframes fadeInLeft {
    from {
        opacity: 0;
        transform: translateX(10%);
    }

    to {
        opacity: 1;
        transform: translateX(0)
    }
}

@keyframes fadeInUpHalfFull {
    from {
        opacity: 0;
        transform: translateY(50%);
    }

    to {
        opacity: 1;
        transform: translateY(0)
    }
}

@keyframes fadeOutUp {
    from {
        opacity: 1;
        transform: translateY(0%);
    }

    to {
        opacity: 0;
        transform: translateY(-20%)
    }
}

@keyframes curtainOutRight {
    from {
        transform: scaleX(1);
    }

    to {
        transform: scaleX(0);
    }
}

@keyframes fadeOutUpHalfFull {
    from {
        opacity: 1;
        transform: translateY(0%);
    }

    to {
        opacity: 0;
        transform: translateY(-50%)
    }
}

@keyframes growIn {
    from {
        transform: scale(0);
    }
    
    to {
        transform: scale(1);
    }
}

@keyframes slideLeft {
    from {
        width: 100%;
    }
    
    to {
        width: 0%;
    }
}

@keyframes float {
    from,
    to {
        transform: translateY(10%);
    }

    25% {
        transform: translateY(0%);
    }

    50% {
        transform: translateY(-10%);
    }

    75% {
        transform: translateY(0%);
    }
}

@keyframes floatMin {
    from,
    to {
        transform: translateY(5%);
    }

    25% {
        transform: translateY(0%);
    }

    50% {
        transform: translateY(-5%);
    }

    75% {
        transform: translateY(0%);
    }
}

@keyframes floatInUpDown {
    from {
        opacity: 0;
        transform: translateY(50%);
    }

    50% {
        transform: translateY(-10%);
    }

    to {
        transform: translateY(0);
        opacity: 1;
    }
}

@keyframes floatInDown {
    from {
        top: -10px;
    }

    to {
        top: 0;
    }
}

@keyframes floatLeft {
    from {
        transform: translateX(0);
    }

    to {
        transform: translateX(-100%);
    }
}

@keyframes floatRight {
    from {
        transform: translateX(0);
    }

    to {
        transform: translateX(100%);
    }
}

@keyframes pulse {
    from,
    to {
        transform: scale(1);
    }

    50% {
        transform: scale(1.5);
    }
}

@keyframes pulse-inwards {
    from,
    to {
        transform: scale(1);
    }

    50% {
        transform: scale(0.5);
    }
}

@keyframes pulseHeart {
    from,
    to {
        transform: scale(1);
    }

    4% {
        transform: scale(1.5);
    }

    8% {
        transform: scale(1);
    }

    12% {
        transform: scale(1.5);
    }

    16%,
    to {
        transform: scale(1);
    }
}

@keyframes jump {
    from,
    to {
        transform: translateY(-10%);
        box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.5);
    }

    50% {
        transform: translateY(0);
        box-shadow: 0 10px 10px -5px rgba(0, 0, 0, 0.5);
    }
}

@keyframes rotate {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

@keyframes sway {
    from,
    to {
        transform: translateX(-5px);
    }

    50% {
        transform: translateX(5px);
    }
}

@keyframes rockH {
    from {
        transform: translateX(0);
    }

    25% {
        transform: translateX(4px);
    }

    50% {
        transform: translateX(0);
    }

    75% {
        transform: translateX(-4px);
    }

    to {
        transform: translateX(0);
    }
}