@use './abstract/colors' as *;

.awe-datepicker {

    &-head {

        &::after,
        &::before {
            content: '';
            position: absolute;
            width: 10px;
            height: 30px;
            border-radius: 10px;
            top: -15px;
        }

        &::after { left: 10px; }
        &::before { right: 10px; }
     }

    &-date,
    .awe-month-option {
        transition: 0.2s;
        cursor: pointer;
        border-radius: 5px;
        
        > div { transition: 0.3s; }

        &:hover,
        &.active {
            
            > div { transform: scale(1.3); }
         }
    }

    .awe-month-picker {
        transform: translateY(calc(100% + 20px));
        transition: 0.5s;

        &.active { transform: translateY(0); }
    }

    .date-bookmark {
        right: 0;
        top: 0;
        padding: 5px;
        background-color: $red;
        border-radius: 5px;
    }
}