@use '../abstract' as *;

.awe-asset-selector {
    border-radius: 5px;
    
    .asset-grid {
        max-width: 1200px;
        width: 100%;
        max-height: 500px;
    }

    .awe-asset-option {
        
        .awe-asset-img-wrapper {
            border-radius: 5px;
            transition: 0.2s;

            img {
                transition: 0.2s;
                max-width: 100%;
            }
        }
        
        .active-indicator {
            position: absolute;
            right: 0px;
            top: 5px;
            background-color: $green;
            border-radius: 50px 0 0 50px;
            padding: 5px 20px 5px 10px;
            transition: 0.3s;
            transform: translateX(100%);
            box-shadow: 10px 5px 5px 0 rgba(0, 0, 0, 0.5);
            
            &.active { transform: translateX(0) }
        }

        &.png-option {
            background-image: url('../../img/bgs/png_bg.jpg');
            background-size: cover;
        }

        &:hover,
        &.active {
            .awe-asset-img-wrapper { border-color: $green; }
        }

        &.active {
            .awe-asset-img-wrapper { transform: scale(0.8); }
        }
    }
}