@use './abstract/index' as *;

.dis {

	&-inline-block { display: inline-block; }

	&-block { display: block; }

	&-none { display: none; }
}

.user-image-container {
	background-color: #ccc;
}

#activitiesLog {

	.activity-log {

		.activity-indicator {

			> div {
				width: 30px;
				height: 30px;
				background: #fff;
				border: 2px solid $lightGray;
				border-radius: 100%;
				position: relative;
			}

			&::before {
				content: "";
				position: absolute;
				left: 0;
				top: 0;
				right: 0;
				margin: 0 auto;
				width: 5px;
				height: 100%;
				background: $lightGray;
			}
		}

		&:last-child {

			.activity-indicator {

				&::before {
					bottom: initial;
					top: 0;
					height: 0%;
				}
			}
		}
	}
}

.logo {

	&-md {
		max-width: 200px;
		width: 100%;
	}
}

.empty-message-container {
	border: 2px dashed #fff;
}

.overlay-modal {
	background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.8));
	z-index: 10;
	transform: scale(0);

	.card {
		width: 100%;
		transition: 0.3s;
		transform: translateY(10px);
		opacity: 0;
	}

	&.in {
		transform: scale(1);

		.card {
			opacity: 1;
			transform: translateY(0);
		}
	}
}

.transitioned {
	transition: $trn-medium;

	&-fast {
		transition: 0.3s;
	}
}

.clickable { cursor: pointer; }

.no-pointer {
	pointer-events: none;
	user-select: none;
	-webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
}

.no-select {
	user-select: none;
	-webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
}

.relative-container {
	position: relative;
}

.sticky-container {
	position: sticky;
}

.absolute {

	&-container {
		position: absolute;
	}

	&-cover-container {
		position: absolute;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
	}

	&-ur {
		top: 5px;
		right: 5px;
	}
}

.fixed {

	&-container {
		position: fixed;
	}

	&-cover-container {
		position: fixed;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
	}
}



.line-height-1 {
	line-height: 1;

	&-5 {
		line-height: 1.5;
	}
}

.bordered {
	
	&-bottom { border-bottom: 1px solid #ccc; }
	&-top { border-top: 1px solid #ccc; }

	&-dashed { border: 2px dashed #ccc; }

	&-color {

		&-success { border-color: $green }
	}
}

.no-whitespace { white-space: nowrap; }

.scroll-view {

	.scroll-idle {
		position: absolute;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
		background: rgba(0, 0, 0, 0.5);

		.mouse-icon {
			width: 25px;
			height: 35px;
			border: 2px solid #fff;
			margin: auto;
			border-radius: 100px;
			position: relative;

			&::after {
				content: '';
				width: 5px;
				height: 5px;
				border-radius: 100%;
				background: #fff;
				margin: auto;
				position: absolute;
				left: 0;
				top: 0;
				right: 0;
				bottom: 0;
				animation-name: moveUpDown;
				animation-duration: 2s;
				animation-iteration-count: infinite;
			}
		}
	}
}

//LOGOUT ELEMENTS
.logout-confirmation {
	.dismiss-btn {
		position: absolute;
		right: 10px;
		top: 10px;
	}
}

.loader-overlay {
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
}