@use '../abstract/colors' as *;

.card {
	border-radius: 7px;
	position: relative;

	&.shadowed { box-shadow: 0 10px 20px -10px rgba(33, 72, 94, 0.3); }

	&.width-controlled {
		width: 100%;
		max-width: 1200px;
		margin: 0 auto;
	}
}

.dark {

	.card {
        background-color: $dark_surface_primary;

        &.card-default { background-color: $dark_surface_default; }
        &.card-secondary { background-color: $dark_surface_secondary; }
        &.card-tertiary { background-color: $dark_surface_tertiary; }

        &.shadowed { box-shadow: 0 10px 20px -5px rgba(0, 0, 0, 0.3); }
    }
}

.light {

	.card {
        background-color: $light_surface_primary;

        &.card-default { background-color: $light_surface_default; }
        &.card-secondary { background-color: $light_surface_secondary; }
        &.card-tertiary { background-color: $light_surface_tertiary; }
    }
}

.status-card {

    &-error,
    &-red {
        border-radius: 5px;
        background-color: transparentize($color: $red , $amount: 0.9);
        border: 2px solid $red;
    }

    &-success,
    &-green {
        border-radius: 5px;
        background-color: transparentize($color: $green , $amount: 0.9);
        border: 2px solid $green;
    }

	&-warning,
    &-orange {
        border-radius: 5px;
        background-color: transparentize($color: $orange , $amount: 0.9);
        border: 2px solid $orange;
    }
}